<template>
  <div>
    <p>{{ list }}</p>
  </div>
</template>

<script>
import request from '../../utils/request';
export default {
  data() {
    return {
      list: {},
    };
  },
  mounted() {
    this.getChartData();
  },
  methods: {
    getChartData() {
      request({
        url: '/admin/login',
        method: 'post',
        params: {
          username: 'asdasd',
          password: 'asdasd',
        },
      }).then(response => {
        this.list = response.data.type;
      });
    },
  },
};
</script>

